<template>
	<div class="side-bar">
    <input type="checkbox" id="switch-location"/>
    <div class="sidebox location">
      <div class="title">Location</div>
      <label class="switch-btn" for="switch-location"></label>
      <div class="content">
        <select name="location" id="location">
          <option value="taiwan">Taiwan</option>
          <option value="USA">USA</option>
        </select>
      </div>
    </div>
    <input type="checkbox" id="switch-Date"/>
    <div class="sidebox date">
      <div class="title">Date</div>
      <label class="switch-btn" for="switch-Date"></label>
      <div class="content">
        <div class="date-interval">
          <div class="from">
            <div class="text">from</div>
            <input type="date" />
          </div>
          <div class="to">
            <div class="text">to</div>
            <input type="date" />
          </div>
        </div>
      </div>
    </div>
    <input type="checkbox" id="switch-Categories"/>
    <div class="sidebox categories">
      <div class="title">Categories</div>
      <label class="switch-btn" for="switch-Categories"></label>
      <div class="content">
        <ul class="cate-lists">
          <li class="cate">
            <input type="checkbox" name="all" id="all">
            <label for="all">All</label>
          </li>
          <li class="cate">
            <input type="checkbox" name="Entertainment" id="Entertainment">
            <label for="Entertainment">Entertainment</label>
          </li>
          <li class="cate">
            <input type="checkbox" name="Food" id="Food">
            <label for="Food">Food</label>
          </li>
          <li class="cate">
            <input type="checkbox" name="Learning" id="Learning">
            <label for="Learning">Learning</label>
          </li>
          <li class="cate">
            <input type="checkbox" name="Outdoors" id="Outdoors">
            <label for="Outdoors">Outdoors</label>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Sidebar',
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
@import '../style/components/sidebar.sass'
</style>


